<template>
  <div class="offfice">

    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/school_list' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{path:'/office_list?id='+$route.query.id}">({{detail.office_name}})</el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--<h5>   {{total}}</h5>-->

    <br/>

    <el-form :inline="true" class="demo-form-inline" :loading="loading" size="mini">


      <el-form-item label="">
        <el-input v-model="search" placeholder="" ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button-group>
          <el-button type="primary" @click="fetch" size="mini"></el-button>
          <el-button  @click="add_form.visible=true" size="mini" :disabled="disabled"></el-button>
          <el-button  @click="$router.push('/office_export?id='+$route.query.id)" size="mini"></el-button>
        </el-button-group>
      </el-form-item>

    </el-form>


    <el-table
      :data="list"
      style="width: 100%"
      border
      size="mini"
      v-loading="loading"
    >

      <el-table-column  label="">
        <template scope="scope">
          {{scope.$index+(page_num-1)*page_size+1}}
        </template>
      </el-table-column>
      <el-table-column prop="total" label=""></el-table-column>
      <el-table-column prop="desc"  label=""></el-table-column>


      <el-table-column prop="time"  label=""></el-table-column>
      <el-table-column   label="" width="80">
        <template scope="scope">

          <el-tag v-if="scope.row.status =='pass' " size="mini"></el-tag>
          <el-tag v-if="scope.row.status =='uncheck' "size="mini" type="danger"></el-tag>
        </template>
      </el-table-column>
      <el-table-column  label=""  width="213">
        <template scope="scope">
          <el-button-group>
            <el-button type="danger" @click="remove(scope.row)" size="mini" :disabled="disabled"></el-button>
            <el-button @click="open_edit(scope.row,scope)" size="mini" :disabled="disabled"></el-button>
            <el-button @click="$router.push('/officeexam2?id='+scope.row.id)" size="mini"></el-button>
          </el-button-group>

        </template>
      </el-table-column>
    </el-table>


    <el-dialog title="" :visible.sync="add_form.visible"
    >
      <el-form class="demo-form-inline" label-width="100px" size="mini">


        <el-form-item label="">
          <el-input v-model="add_form.total" placeholder=""></el-input>
        </el-form-item>



        <el-form-item label="">
          <el-date-picker
            v-model="add_form.time"
            align="right"
            type="date"
            placeholder=""
            format="yyyy-MM-dd"
           >
          </el-date-picker>


        </el-form-item>

        <el-form-item label="">
          <el-input   v-model="add_form.desc" placeholder=""></el-input>
        </el-form-item>


      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="add_form.visible=false" size="small"> </el-button>
          <el-button type="primary" @click="add" size="small"> </el-button>
        </span>
    </el-dialog>


    <el-dialog title="" :visible.sync="edit_form.visible"
    >
      <el-form class="demo-form-inline"  label-width="100px" size="mini">


        <el-form-item label="">
          <el-input v-model="edit_form.total" placeholder=""></el-input>
        </el-form-item>



        <el-form-item label="">
          <el-date-picker
            v-model="edit_form.time"
            align="right"
            type="date"
            placeholder=""
            format="yyyy-MM-dd"
          >
          </el-date-picker>


        </el-form-item>

        <el-form-item label="">
          <el-input   v-model="edit_form.desc" placeholder=""></el-input>
        </el-form-item>
      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="edit_form.visible=false" size="mini"> </el-button>
          <el-button type="primary" @click="edit" size="mini"> </el-button>
        </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="upload_dialog.visible"
               size="small"


    >
      <el-row>
        <el-button size="small"></el-button>
      </el-row>
      <br/>



      <el-upload
        class="upload-demo"
        drag
        style="width: 100%"
        action="https://jsonplaceholder.typicode.com/posts/"
        multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
      </el-upload>


      <span slot="footer" class="dialog-footer">
          <el-button @click="upload_dialog.visible=false"> </el-button>
          <el-button type="primary" @click="edit"> </el-button>
        </span>
    </el-dialog>



    <br>
    <div style="display: inline-block;float: right">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="(p)=>page_num=p"
        :current-page="page_num"
        :page-size="page_size"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>


  import request from './config'
  import moment from 'moment'

  export default {

    name: 'app',
    data () {
      return {
        disabled:!window.type.office,
        list:[],
        detail:{},
        total:0,
        search:'',
        page_size:10,
        page_num:1,
        loading:false,
        add_form:{
          total:'',
          desc:'',
          time:'',
          visible:false
        },
        edit_form:{
          id:'',
          total:'',
          desc:'',
          time:'',
          visible:false
        },
        active_school_id:'-1',
        school_list:[],
        upload_dialog:{
          visible:false
        },
        user:window.user
      }
    },

    watch:{
      page_num(){
        this.fetch()
      }
    },

    methods:{
      change_page(p){
        console.log(p)
      },
      open_edit(data,b){
        console.log(data,b)
        data.visible = true
        this.edit_form = {
          visible:true,
          id:data.id,
          total:data.total,
          desc:data.desc,
          time:data.time,
        }
      },
      async fetch(){

        var page_size = this.page_size,
          page_num = this.page_num,
          search = this.search,
          that = this


        this.loading = true
        var id = this.$route.query.id
        var response = await request.get('/api/exam/?office_id='+id,{
          params:{
            page_size,
            page_num,
            search
          }
        })
        this.list = response.data.objects
        var page  = response.data.page
        this.page_num = page.page_num
        this.page_size = page.page_size
        this.total = page.total
        this.loading = false
      },
      async add(){
        var add_form = this.add_form
        add_form.office_id = window.office.id
        add_form.time = moment(add_form.time).format('YYYY-MM-DD')


        console.log(add_form,'add_form')

        var response = await request.post('/api/exam/',add_form)
        this.fetch()
        this.add_form.visible = false
        this.$message({
          type: 'success',
          message: ''
        });
      },
      async remove(data){
        try {
          await this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })
          var response = await request.delete('/api/exam/'+data.id+'/')

          this.$message({
            type: 'success',
            message: '!'
          });
          this.fetch()
        }catch (e){
          this.$message({
            type: 'info',
            message: ''
          });
        }

      },
      async edit(){
        var edit_form = this.edit_form
        console.log(edit_form)
        var id = edit_form.id
        edit_form.time = moment(edit_form.time).format('YYYY-MM-DD')
        edit_form.office_id = window.office.id


        var response = await request.put('/api/exam/'+id +'/',edit_form)
        this.edit_form.visible = false
        this.$message({
          type: 'success',
          message: '!'
        });
        this.fetch()
      },

      async fetch_office(){
        var id = this.$route.query.id
        var response = await request.get('/api/office/'+id+'/')
        this.detail = response.data
        console.log(response.data)
      },

    },
    async mounted(){

      await this.fetch()
      await this.fetch_office()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>



</style>



